.link {
  background-color: $basic_green;
  color: $basic-white;
  text-decoration: none;
  text-transform: uppercase;
  z-index: 10;
  font-weight: normal;

  &:focus {
    outline: none;
  }

  &:hover,
  &:focus {
    background-color: $basic_green-2;
  }

  &:active {
    background-color: $basic_green-2;
    color: transparentize($basic-white, 0.7);
  }
}

